import last from "lodash/last"
import { SVGPathData } from "svg-pathdata"










import { V as VV } from "ts3dutils"

export function H(x) {
  return { type: SVGPathData.HORIZ_LINE_TO, relative: false, x: x }
}

export function A(
  rx,
  ry,
  xAxisRotation,
  largeArcFlag,
  sweepFlag,
  p,
) {
  return {
    lArcFlag: largeArcFlag,
    rX: rx,
    rY: ry,
    relative: false,
    sweepFlag: sweepFlag,
    type: SVGPathData.ARC,
    x: p.x,
    xRot: 0,
    y: p.y,
  }
}

export const C = (
  x1,
  y1,
  x2,
  y2,
  x,
  y,
) => ({
  type: SVGPathData.CURVE_TO,
  x1,
  y1,
  x2,
  y2,
  x,
  y,
  relative: false,
})

export const V = (y) => ({
  type: SVGPathData.VERT_LINE_TO,
  y,
  relative: false,
})

export const S = (x2, y2, x, y) => ({
  type: SVGPathData.SMOOTH_CURVE_TO,
  x2,
  y2,
  x,
  y,
  relative: false,
})



export function M(a1, a2) {
  const [x, y] = a2 === undefined ? [(a1 ).x, (a1 ).y] : [a1, a2]
  return { type: SVGPathData.MOVE_TO, x, y, relative: false }
}

export const m = (x, y) => ({
  type: SVGPathData.MOVE_TO,
  x,
  y,
  relative: true,
})



export function L(a1, a2) {
  const [x, y] = a2 === undefined ? [(a1 ).x, (a1 ).y] : [a1, a2]
  return { type: SVGPathData.LINE_TO, x, y, relative: false }
}

export const l = (x, y) => ({
  type: SVGPathData.LINE_TO,
  x,
  y,
  relative: true,
})

export const Z = () => ({ type: SVGPathData.CLOSE_PATH })

export const encode = (...path) => {
  const modPath = []
  let lastX = 0,
    lastY = 0
  for (const c of path) {
    if ("function" === typeof c) {
      for (const c2 of c(lastX, lastY)) {
        modPath.push(c2)
      }
    } else {
      modPath.push(c)
    }
    const lastC = last(modPath)
    if ("x" in lastC) lastX = lastC.x
    if ("y" in lastC) lastY = lastC.y
  }
  return new SVGPathData(modPath).round(3).encode()
}
 

export function Corner(rad, x, y) {
  return (prevX, prevY) => {
    const prev = VV(prevX, prevY)
    const p1 = VV(x, y)
    const dir1 = prev.to(p1).toLength(rad)
    const p2 = VV(p1.x + dir1.y, p1.y - dir1.x)
    return [L(p1.minus(dir1)), A(rad, rad, 0, 0, 0, p2)]
  }
}
